.Header-Logo {
    width: 90px;
    height: 21px;
    margin: 0 auto 30px;
    background-image: url('Header-Logo.png');
}

@media (-webkit-min-device-pixel-ratio: 1.5), (-o-min-device-pixel-ratio: 3/2), (min-resolution: 1.5dppx) {

    .Header-Logo {
        background-image: url('Header-Logo@2x.png');
    }
}



